<script>
  import bCarousel from 'bootstrap-vue/es/components/carousel/carousel';
  import bImg from 'bootstrap-vue/es/components/image/img';
  import bMedia from 'bootstrap-vue/es/components/media/media';

  import mixins from '../../mixins/index';

  import Header from '../../component/header/index';
  import Footer from '../../component/footer/index';

  export default {
    components: {
      bCarousel,
      bImg,
      bMedia,
      Header,
      Footer
    },
    mixins: [mixins],
    data: () => ({
      slide: 0,
      scrollIndex: 1,

    }),
    created() {

    },
    mounted() {
      this.windowScrool();
      setTimeout(() => {
        window.scrollTo(0, this.$route.query.top);
      }, 500)
    },
    methods: {
      onSlideStart (slide) {
        this.sliding = true
      },
      onSlideEnd (slide) {
        this.sliding = false
      },
      windowScrool: function () {
        window.onscroll = () => {
          var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          if (scrollTop <= 653){
            this.scrollIndex = 0;
          }
          if (scrollTop > 653 && scrollTop <= 1000){
            this.scrollIndex = 1;
          }
          else if (scrollTop > 1000 && scrollTop <= 1138){
            this.scrollIndex = 2;
          }
          else if (scrollTop > 1138){
            this.scrollIndex = 2;
          }
        }
      },
      handleScrool: function (id) {
        if(id === 0){
          window.scrollTo(0, 0);
          this.scrollIndex = id;
        }
        if(id === 1){
          window.scrollTo(0, 668);
          this.scrollIndex = id;
        }
        if(id === 2){
          window.scrollTo(0, 1138);
          this.scrollIndex = id;
        }
        if(id === 3){
          window.scrollTo(0, 0);
          this.scrollIndex = 1;
        }
      }
    }
  }
</script>



