
<template>
  <div>
    <!--  -->
    <Header :index="0"
            :isShowCategoryLeft="false"></Header>
    <div class="container">
      <div class="breadcrumb">
        <b-img class="navigation-home-image"
               width="16"
               height="16"
               style="margin-right: 12px;"
               src="../static/image/navigation-home.png"
               alt=""/>
        <router-link class="breadcrumb-a" to="/index"></router-link>
        <span class="breadcrumb-line">/</span>
        
      </div>
    </div>
    <div class="container main">
      <Sidebar :thisPage="0"></Sidebar>
      <div class="conter">
        <div class="content-description">
          <h2 class="content-description-title"></h2>
          <div class="address">
            <div class="address-list">
              <div class="address-list-content">
                <p>{{order.saleOrderId}}</p>
                <p>{{order.systemCreateTime}}</p>
                <p>{{
                  order.saleOrderStatus === 'WAITING_DELIVERY' ?
                  '' : order.saleOrderStatus === 'WAITING_PAID' ?
                  '' : order.saleOrderStatus === 'WAITING_RECEIVE' ?
                  '' : '' }}</p>
                <p>: {{order.saleOrderIsOpenInvoice ? '' : ''}}</p>
              </div>
              <div class="address-list-right">
                <p>{{order.saleOrderPayWay === 'ALI_PAY' ? '' : order.saleOrderPayWay === 'WX_PAY' ? '' : ''}}</p>
              </div>
            </div>
          </div>
          <div class="address">
            <div class="address-list">
              <div class="address-list-content">
                <p>{{order.saleOrderReceiveName}}</p>
                <p>{{order.saleOrderReceiveMobile}}</p>
                <p>{{order.saleOrderReceiveProvince}}</p>
                <p>{{order.saleOrderReceiveCity}}</p>
                <p>{{order.saleOrderReceiveArea}}</p>
                <p>{{order.saleOrderReceiveAddress}}</p>
              </div>
              <div class="address-list-right">
                <p>{{order.saleOrderExpressAmount}}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="product">
          <div class="product-title"></div>
          <div class="product-list">
            <div class="product-list-title">
              <div class="product-list-title-image"></div>
              <div class="product-list-title-name"></div>
              <div class="product-list-title-number"></div>
              <div class="product-list-title-price"></div>
              <div class="product-list-title-total"></div>
            </div>
            <div class="product-list-body"
                 v-for="product in order.saleOrderProductList">
              <div class="product-list-body-image">
                <b-img class="border"
                       style="width: 100px"
                       :src="imageHost + product.productImagePath"
                       alt=""/>
              </div>
              <div class="product-list-body-name">{{product.productTitle}}</div>
              <div class="product-list-body-number">{{product.productQuantity}}</div>
              <div class="product-list-body-price">{{product.productPrice}}</div>
              <div class="product-list-body-total">{{product.productOriginalPrice}}</div>
            </div>
          </div>
        </div>
        <div class="logistics" v-if="order.saleOrderExpressList && order.saleOrderExpressList.length > 0">
          <div class="logistics-title"></div>
          <div class="">
            <div class="logistics-item" v-for="expressTrace in order.saleOrderExpressList[0].expressTraces">
              {{expressTrace.time}}    {{expressTrace.context}}
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <Footer></Footer>
  </div>

</template>

<style scoped>
  .breadcrumb{
    margin: 30px 0px;
  }
  .breadcrumb-icon{
    width: 20px;
    height: 20px;
  }
  .breadcrumb-line{
    margin: 0px 10px;
  }
  .breadcrumb-a{
    color: #1a1a1a;
  }
  .breadcrumb-a:hover{
    color: #999;
  }
  .main{
    display: flex;
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .conter{
    flex: 1;
  }

  .content-description-title{
    font-size: 22px;
    margin: 20px 0px 20px 0px;
    color: #666666;
  }

  .address{
    border-top: 1px solid #dddddd;
    border-left: 1px solid #dddddd;
    border-right: 1px solid #dddddd;
  }
  .address-list{
    display: flex;
    border-bottom: 1px solid #dddddd;
  }
  .address-list-content{
    width: 50%;
    padding: 8px;
    border-right: 1px solid #dddddd;
  }
  .address-list-right{
    width: 50%;
    padding: 8px;
  }

  .product{
    margin-top: 60px;
  }

  .product-title{
    font-size: 22px;
    margin-bottom: 20px;
    color: #666666;
  }

  .product-list-title{
    border-top: 1px solid #dddddd;
  }

  .product-list-title-image{
    width: 180px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-title-name{
    width: 360px;
    padding: 8px;
    text-align: left;
    display: inline-block;
  }

  .product-list-title-number{
    width: 91px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-title-price{
    width: 124px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-title-total{
    width: 91px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-body{
    border-top: 1px solid #dddddd;
  }

  .product-list-body-image{
    width: 180px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-body-name{
    width: 360px;
    padding: 8px;
    text-align: left;
    display: inline-block;
  }

  .product-list-body-number{
    width: 91px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-body-price{
    width: 124px;
    padding: 8px;
    display: inline-block;
  }

  .product-list-body-total{
    width: 91px;
    padding: 8px;
    display: inline-block;
  }

  .logistics{
    margin-bottom: 40px;
  }

  .logistics-title{
    font-size: 22px;
    padding: 60px 0px 10px 0px;
    color: #666666;
  }

  .logistics-body{
    padding-bottom: 20px;
  }

  .logistics-item{
    line-height: 30px;
    border-bottom: 1px solid #dddddd;
  }
</style>

<script>
  import bImg from 'bootstrap-vue/es/components/image/img';

  import mixins from '../../mixins/index';
  import moment from 'moment';

  import Header from '../../component/header/index';
  import Footer from '../../component/footer/index';
  import Sidebar from '../../component/member/sidebar';

  export default {
    components: {
      bImg,
      Header,
      Footer,
      Sidebar
    },
    mixins: [mixins],
    data: () => ({
      saleOrderId: '',
      order: {

      }
    }),
    created() {
    },
    mounted() {
      if (this.$route.params.saleOrderId) {
        this.saleOrderId = this.$route.params.saleOrderId;
        this.handleLoadProduct();
      }

    },
    methods: {
      handleLoadProduct() {
        this.request({
          url: '/xingxiao/sale/order/desktop/v1/find',
          data: {
            saleOrderId: this.saleOrderId
          },
          success: (data) => {
            data.systemCreateTime = moment(data.systemCreateTime).format("YYYY-MM-DD");
            data.saleOrderProductList[0].productPrice = data.saleOrderProductList[0].productPrice.toFixed(2);
            data.saleOrderProductList[0].productOriginalPrice = data.saleOrderProductList[0].productOriginalPrice.toFixed(2);
            this.order = data;
          },
          error: () => {

          }
        });
      }
    }
  }
</script>



